<template>
  <div id="container">

    <div id="take-part">
      <div class="tp-bg"></div>
      <div class="tp-title">Принять участие</div>
      <div class="tp-pic">
        <img src="../files/participation.svg" alt="Take Part">
      </div>
      <div class="tp-desc">В своём стремлении<br>улучшить пользовательский..</div>
      <div class="tp-form">
        <div class="btn tp-ticket-rm" id="removeTicket" @click.prevent="ticketCount < 1 ? 0 : ticketCount--">
          <ion-icon name="remove"></ion-icon>
        </div>
        <div class="tp-ticket-count" id="countTicket">0</div>
        <div class="btn tp-ticket-add" id="addTicket" @click.prevent="ticketCount++">
          <ion-icon name="add"></ion-icon>
        </div>
      </div>
      <div class="ticket-buy-btn" @click="createTicket">Участвовать</div>
    </div>

    <BottomNavigationMenu active="participation" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";

export default {
  name: "Participation",
  data: () => ({
    user_id: 0,
    ticketCount: 0,
  }),
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  mounted() {
    document.title = 'Принять участие';
    const telegram = window.Telegram.WebApp;
    const userData = telegram.initDataUnsafe.user;

    this.user_id = userData.id
  },
  methods: {
    async createTicket() {
      const telegram = window.Telegram.WebApp;
      await axios.post('https://back.sombra.uz/api/ticket/' + this.user_id, {
        tickets_count: this.ticketCount
      })
      .then(response => {
        if (response.data.status === "success" && this.ticketCount !== 0) {
          telegram.showPopup({
            'title': 'Участие',
            'message': `Вам успешно начислили ${this.ticketCount} тикетов`,
            'buttons': [
              {
                'id': 'success',
                'type':'ok',
                'text': "Продолжить"
              }
            ]
          })
        } else {
          telegram.showPopup({
            'title': 'Участие',
            'message': `Произлашло ошибка, повторите позже`,
            'buttons': [
              {
                'id': 'error',
                'type':'ok',
                'text': "Повторить"
              }
            ]
          })
        }
        this.ticketCount = 0
      }).catch(() => {
        this.ticketCount = 0
      })
    }
  },
  watch: {
    ticketCount() {
      document.getElementById('countTicket').innerText = this.ticketCount
    }
  }
}
</script>

<style scoped>

@media screen and (max-height: 748px) {
  #container {
    padding: 0 25px;
    margin-bottom: 110px !important;
  }
}

@media screen and (max-height: 652px) {
  #container {
    padding: 0 25px;
    margin-bottom: 210px !important;
  }
}
</style>