<template>
  <div id="header">
    <div class="user-menu">
      <div class="user-pic">
        <img :src="photo" :alt="first_name">
      </div>
      <div class="user-name">{{ this.first_name }}</div>
    </div>
    <div class="user-tools">
      <router-link tag="div" to="/settings" class="btn-icon settings">
        <ion-icon name="cog-outline" class="ai-icon"></ion-icon>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "HeaderUserBar",
  data: () => ({
    user_id: 0,
    photo: '',
    first_name: '',
  }),
  mounted() {
    const userData = window.Telegram.WebApp.initDataUnsafe.user;

    this.user_id = userData.id

    axios
        .get('https://back.sombra.uz/api/user/' + this.user_id)
        .then(response => {
          if (response.data.data) {
            this.photo = response.data.data.photo
            this.first_name = response.data.data.first_name
          }
        })
        .catch(error => console.log(error));
  }
}
</script>

